<template>
    <v-container>
        <v-row>
            <v-col cols="12">

                <v-data-iterator :items="products" hide-default-footer>
                    <template v-slot:default="props">
                        <v-row class="product-container">
                            <v-col class="product" v-for="item in props.items" :key="item.id">
                                <v-hover v-slot="{ hover }" open-delay="100">
                                    <v-card class="product-card" @click.stop="$router.push('/product')" :elevation="hover ? 16 : 2"
                                        :class="{ 'on-hover': hover }">
                                        <v-img class="product-img" v-bind:src="item.images"></v-img>
                                        <v-card-title class="subheading justify-center font-weight-bold">
                                            {{ item.name }}
                                        </v-card-title>
                                        <p>{{ item.description }}</p>
                                    </v-card>
                                </v-hover>
                            </v-col>
                        </v-row>
                    </template>
                </v-data-iterator>
            </v-col>
        </v-row>

    </v-container>
</template>

<script>

import { appConfig } from '../services/productService';

export default {
    name: 'ProductsView',
    data: () => ({
        products: []
    }),
    components: {
    },
    created() {
        console.log("created");
        this.products = appConfig.state;
    },
    mounted() {
        console.log("mounted");
        this.products = appConfig.state;
    },
}
</script>

<style scoped>
.product-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.product {
    flex-grow: 0;
}

.product-img {
    max-height: 200px;
    max-width: 200px;
    margin: auto;
}

p {
    text-align: center;
    margin-inline: 5px;
}

@media only screen and (max-width: 960px) {
    .product {
        flex-grow: 1;
    }

    .product-card{
        margin-inline: 10px;
        padding-inline: 50px;
    }
}
</style>

<style lang="sass" scoped>
.v-card.on-hover.theme--dark
  background-color: rgba(#FFF, 0.8)
  >.v-card__text
    color: #000
</style>